import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/query/react";
var kinopoiskApiKey = process.env.API_KEY;
var kinopoiskApiBaseUrl = process.env.BASE_API_URL;
export var searchMovieApi = createApi({
    reducerPath: "searchMovieApi",
    baseQuery: fetchBaseQuery({
        baseUrl: kinopoiskApiBaseUrl,
        prepareHeaders: function (headers) {
            headers.set("X-API-KEY", kinopoiskApiKey);
            headers.set("Content-Type", "application/json");
            return headers;
        },
    }),
    endpoints: function (builder) {
        return ({
            getSearchMovie: builder.query({
                query: function (keyword) {
                    return "/v2.2/films?order=RATING&type=ALL&ratingFrom=0&ratingTo=10&yearFrom=1000&yearTo=3000&keyword=".concat(keyword, "&page=1");
                },
            }),
        });
    },
});
export var useGetSearchMovieQuery = searchMovieApi.useGetSearchMovieQuery;
